<template>
  <div>
    <b-form @submit.prevent="submit">
      <b-row>
        <b-col cols="12" xl="2">
          <label class="highLightHeaderLabel">{{
            $t("INPUTEXPENSEITEM.EXPENSEINFORMATION")
          }}</label
          >&nbsp;&nbsp;
          <b-button variant="primary" class="px-16 required-btnAny">{{
            $t("REQUIREDBUTTON")
          }}</b-button>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12" xl="1">
          <label class="highLightLabel"
            >&nbsp;&nbsp;{{
              selected.account ? selected.account.name : ""
            }}</label
          >
        </b-col>
        <b-col cols="12" xl="2">
          <b-form-input
            v-model="selected.account.value"
            :disabled="true"
          ></b-form-input>
          <show-errors
            :control="$v.selected.account.value"
            :field="$t('EXPENSEITEMACCOUNTERROR')"
          />
        </b-col>
        <b-col cols="12" xl="1">
          <label class="highLightLabel">{{ selected.expenseItem.name }}</label>
        </b-col>
        <b-col cols="12" xl="2">
          <b-form-input
            :disabled="true"
            v-model="selected.expenseItem.value"
          ></b-form-input>
          <show-errors
            :control="$v.selected.expenseItem.value"
            :field="$t('EXPENSEITEMNAMEERROR')"
          />
        </b-col>
        <b-col cols="12" xl="1">
          <label class="highLightLabel">{{ selected.expense.name }}</label>
        </b-col>
        <b-col cols="12" xl="2">
          <b-form-input
            :disabled="false"
            v-model.trim="selected.expense.value"
            maxlength="9"
            onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
          ></b-form-input>
          <show-errors
            :control="$v.selected.expense.value"
            :field="$t('EXPENSEITEMCOSTERROR')"
          />
        </b-col>
      </b-row>

      <b-row>
        <b-col cols="12" xl="2">
          <template v-if="relatedFlagInfo">
            <label class="highLightHeaderLabel">{{
              $t("INPUTEXPENSEITEM.RELATEDINFORMATION")
            }}</label
            >&nbsp;&nbsp;
            <b-button variant="outline-primary" class="px-16 required-btnAny">{{
              $t("INPUTEXPENSEITEM.ANYBUTTON")
            }}</b-button>
          </template>
        </b-col>
      </b-row>

      <b-row>
        <template
          v-if="
            selected.expansionItem1 != undefined &&
            selected.expansionItem1.name != '-'
          "
        >
          <b-col cols="12" xl="1">
            <label class="highLightLabel">{{
              selected.expansionItem1.name
            }}</label>
          </b-col>
          <b-col cols="12" xl="2">
            <b-form-input
              v-model="selected.expansionItem1.value"
              :id="
                selected.expansionItem1.id
                  ? `'${selected.expansionItem1.id}'`
                  : ''
              "
              :type="selected.expansionItem1.kbn == '01' ? 'text' : 'number'"
              oninput="javascript: if (this.value.length > this.maxLength) this.value = this.value.slice(0, this.maxLength);"
              maxlength="12"
            ></b-form-input>
          </b-col>
        </template>
        <template
          v-if="
            selected.expansionItem2 != undefined &&
            selected.expansionItem2.name != '-'
          "
        >
          <b-col cols="12" xl="1">
            <label class="highLightLabel">{{
              selected.expansionItem2.name
            }}</label>
          </b-col>
          <b-col cols="12" xl="2">
            <b-form-input
              v-model="selected.expansionItem2.value"
              :id="
                selected.expansionItem2.id
                  ? `'${selected.expansionItem2.id}'`
                  : ''
              "
              :type="selected.expansionItem2.kbn == '01' ? 'text' : 'number'"
              oninput="javascript: if (this.value.length > this.maxLength) this.value = this.value.slice(0, this.maxLength);"
              maxlength="12"
            ></b-form-input>
          </b-col>
        </template>
        <template
          v-if="
            selected.expansionItem3 != undefined &&
            selected.expansionItem3.name != '-'
          "
        >
          <b-col cols="12" xl="1">
            <label class="highLightLabel">{{
              selected.expansionItem3.name
            }}</label>
          </b-col>
          <b-col cols="12" xl="2">
            <b-form-input
              v-model="selected.expansionItem3.value"
              :id="
                selected.expansionItem3.id
                  ? `'${selected.expansionItem3.id}'`
                  : ''
              "
              :type="selected.expansionItem3.kbn == '01' ? 'text' : 'number'"
              oninput="javascript: if (this.value.length > this.maxLength) this.value = this.value.slice(0, this.maxLength);"
              maxlength="12"
            ></b-form-input>
          </b-col>
        </template>
        <template
          v-if="
            selected.expansionItem4 != undefined &&
            selected.expansionItem4.name != '-'
          "
        >
          <b-col cols="12" xl="1">
            <label class="highLightLabel">{{
              selected.expansionItem4.name
            }}</label>
          </b-col>
          <b-col cols="12" xl="2">
            <b-form-input
              v-model="selected.expansionItem4.value"
              :id="
                selected.expansionItem4.id
                  ? `'${selected.expansionItem4.id}'`
                  : ''
              "
              :type="selected.expansionItem4.kbn == '01' ? 'text' : 'number'"
              oninput="javascript: if (this.value.length > this.maxLength) this.value = this.value.slice(0, this.maxLength);"
              maxlength="12"
            ></b-form-input>
          </b-col>
        </template>
        <template
          v-if="
            selected.expansionItem5 != undefined &&
            selected.expansionItem5.name != '-'
          "
        >
          <b-col cols="12" xl="1">
            <label class="highLightLabel">{{
              selected.expansionItem5.name
            }}</label>
          </b-col>
          <b-col cols="12" xl="2">
            <b-form-input
              v-model="selected.expansionItem5.value"
              :id="
                selected.expansionItem5.id
                  ? `'${selected.expansionItem5.id}'`
                  : ''
              "
              :type="selected.expansionItem5.kbn == '01' ? 'text' : 'number'"
              oninput="javascript: if (this.value.length > this.maxLength) this.value = this.value.slice(0, this.maxLength);"
              maxlength="12"
            ></b-form-input>
          </b-col>
        </template>
        <template
          v-if="
            selected.expansionItem6 != undefined &&
            selected.expansionItem6.name != '-'
          "
        >
          <b-col cols="12" xl="1">
            <label class="highLightLabel">{{
              selected.expansionItem6.name
            }}</label>
          </b-col>
          <b-col cols="12" xl="2">
            <b-form-input
              v-model="selected.expansionItem6.value"
              :id="
                selected.expansionItem6.id
                  ? `'${selected.expansionItem6.id}'`
                  : ''
              "
              :type="selected.expansionItem6.kbn == '01' ? 'text' : 'number'"
              oninput="javascript: if (this.value.length > this.maxLength) this.value = this.value.slice(0, this.maxLength);"
              maxlength="12"
            ></b-form-input> </b-col
        ></template>
      </b-row>
      <br />
      <b-row>
        <b-col cols="12">
          <b-row>
            <b-col cols="5" xl="2" class="mb-1">
              <b-button @click="clearExpenseForm" variant="primary">{{
                $t("CLEAR")
              }}</b-button>
            </b-col>
            <b-col cols="7" xl="2" class="mb-1">
              <b-button @click="registerExpenseItem" variant="primary"
                >{{ $t("REGISTER")
                }}<i v-if="loading" class="fa fa-spinner fa-spin"
              /></b-button>
            </b-col>
            <b-col cols="5" xl="2" class="mb-1">
              <b-button @click="addExpenseItem" variant="primary">{{
                $t("ADDEXPENSEITEM")
              }}</b-button>
            </b-col>
            <b-col cols="7" xl="6" class="mb-1">
              <b-button
                @click="Journalstatementimport"
                variant="primary"
                class="float-sm-left float-lg-right"
                >{{ $t("Journalstatementimport") }}</b-button
              >
            </b-col>
          </b-row>
        </b-col>
      </b-row>
    </b-form>
    <hr class="hrBarType" />
    <b-row>
      <b-col cols="12">
        <b-row>
          <b-col>
            <div class="pointer d-flex align-items-center">
              <b-link
                :disabled="checkFlag"
                @click="onPreviousClick"
                class="d-flex align-items-center pointer"
              >
                <span class="px-2">
                  <i
                    class="fa fa-caret-left fa-2x text-muted"
                    aria-hidden="true"
                  ></i>
                </span>
                <span class="font-weight-bold"
                  >{{ $t("INPUTEXPENSEITEM.PREVIOUSMONTH") }} &nbsp;&nbsp;</span
                >
              </b-link>
            </div>
          </b-col>

          <b-col>
            <div class="pointer align-items-center text-center">
              <span class="font-weight-bold"
                >{{
                  financialYear + $t("YEARJP") + month + $t("YEARMONTH")
                }}&nbsp;&nbsp;</span
              >
            </div>
          </b-col>

          <b-col>
            <div class="pointer d-flex align-items-center float-right">
              <b-link
                :disabled="checkFlag"
                @click="onNextClick"
                class="d-flex align-items-center pointer"
              >
                <span class="font-weight-bold"
                  >{{ $t("INPUTEXPENSEITEM.NEXTMONTH") }} &nbsp;&nbsp;</span
                >

                <span @click="onNextClick" class="px-2">
                  <i
                    class="fa fa-caret-right fa-2x text-muted"
                    aria-hidden="true"
                  ></i>
                </span>
              </b-link>
            </div>
          </b-col>
        </b-row>
      </b-col>
    </b-row>
    <b-table
      ref="selectableTable"
      :select-mode="selectMode"
      :fields="fields"
      :busy="isBusy"
      :items="inputExpenseItemTableData"
      :borderless="false"
      :outlined="false"
      :bordered="false"
      :striped="false"
      :tbody-tr-class="rowClass"
      fixed
      sticky-header
      small="small"
      responsive="sm"
      head-variant="light"
    >
      <template v-slot:cell(selectRow)="item">
        <template>
          <b-form-radio
            :id="'radio-' + item.index"
            v-model="item.item.selectRow"
            :name="'table-radio'"
            @change="toggleSelectAll(item)"
          ></b-form-radio>
        </template>
      </template>
      <template v-slot:cell(account)="item">{{ item.value.value }}</template>
      <template v-slot:cell(expenseItem)="item">{{
        item.value.value
      }}</template>
      <template v-slot:cell(expense)="item">{{ item.value.value }}</template>
      <template v-slot:cell(expansionItem1)="item">
        <div>{{ item.value.name }}</div>
        <div>{{ item.value.value }}</div>
      </template>
      <template v-slot:cell(expansionItem2)="item">
        <div>{{ item.value.name }}</div>
        <div>{{ item.value.value }}</div>
      </template>
      <template v-slot:cell(expansionItem3)="item">
        <div>{{ item.value.name }}</div>
        <div>{{ item.value.value }}</div></template
      >
      <template v-slot:cell(expansionItem4)="item">
        <div>{{ item.value.name }}</div>
        <div>{{ item.value.value }}</div>
      </template>
      <template v-slot:cell(expansionItem5)="item">
        <div>{{ item.value.name }}</div>
        <div>{{ item.value.value }}</div>
      </template>
      <template v-slot:cell(expansionItem6)="item">
        <div>{{ item.value.name }}</div>
        <div>{{ item.value.value }}</div>
      </template>
      <template v-slot:table-busy>
        <div class="text-center text-danger my-2">
          <b-spinner class="align-middle"></b-spinner>
        </div>
      </template>
    </b-table>
    <div class="text-center text-danger mb-2 mt-2">{{ errorMessage }}</div>
    <b-modal
      id="tree-modal"
      ref="tree-modal"
      :hide-footer="true"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="py-0"
      size="md"
      footer-class="border-0"
      header-class="border-0 bg-white pd-0"
      button-size="sm"
      hide-header-close
    >
      <template v-slot:modal-header="{ cancel }">
        <div class="title-fs font-weight-bold mt-2 text-dark ml-3">
          {{ $t("ADDEXPENSEITEM") }}
        </div>
        <b-button
          @click="cancel"
          class="back-btn close-btn mt-2"
          variant="primary"
          >{{ $t("CLOSE") }}</b-button
        >
      </template>
      <b-card class="pop-up-box">
        <b-row>
          <b-col>
            <SubMenuTree
              @update-expense-type="updateExpenseItem"
              :tree-options="treeOptions"
              :disabledItems="disabledExpenseItems"
              :treeTitle="addExpenseTitle"
            />
          </b-col>
        </b-row>
      </b-card>
      <b-row>
        <b-col cols="12" class="text-center mb-2">
          <b-button
            :disabled="selectedTreeItem.length <= 0"
            @click="getExpansionItem"
            variant="primary"
            class="float-right"
            >{{ $t("ADD") }}
            <i v-if="selectLoader" class="fa fa-spinner fa-spin" />
          </b-button>
        </b-col>
      </b-row>
    </b-modal>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";
import {mustBeGreaterThanZero} from "@/shared/customValidator";
import SubMenuTree from "@/components/SubMenuTree.vue";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import ShowErrors from "@/components/ShowErrors";
import { getDateHourMinute } from "@/shared/utils";
import { mapGetters } from "vuex";
import { Settings } from "luxon";
Settings.defaultLocale = "ja";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "ExpenseItemTab",
  components: { SubMenuTree, ShowErrors },
  data() {
    return {
      errorMessage: "",
      checkFlag: false,
      inputExpenseItemTableData: [],
      treeOptions: {
        checkbox: false,
      },
      agencyFlag: false,
      isBusy: false,
      loading: false,
      selectLoader: false,
      yearStartMonth: "",
      financialYear: "",
      addExpenseTitle: "",
      manipulateData: [],
      allTreeData: [],
      year: "",
      month: "",
      selectMode: "single",
      selectedTreeItem: [],
      selectedExpenseItem: "",
      disabledExpenseItems: [],
      relatedFlagInfo: false,
      formStyle: {
        account: {
          name: this.$t("INPUTEXPENSE.account"),
          value: "",
        },
        expenseItem: {
          name: this.$t("INPUTEXPENSE.expenseItem"),
          value: "",
        },
        expense: {
          name: this.$t("INPUTEXPENSE.expense"),
          value: "",
        },
        expansionItem1: {
          name: "-",
          value: "-",
        },
        expansionItem2: {
          name: "-",
          value: "-",
        },
        expansionItem3: {
          name: "-",
          value: "-",
        },
        expansionItem4: {
          name: "-",
          value: "-",
        },
        expansionItem5: {
          name: "-",
          value: "-",
        },
        expansionItem6: {
          name: "-",
          value: "-",
        },
      },
      selected: {
        account: {
          name: this.$t("INPUTEXPENSE.account"),
          value: "",
        },
        expenseItem: {
          name: this.$t("INPUTEXPENSE.expenseItem"),
          value: "",
        },
        expense: {
          name: this.$t("INPUTEXPENSE.expense"),
          value: "",
        },
        expansionItem1: {
          name: "-",
          value: "-",
        },
        expansionItem2: {
          name: "-",
          value: "-",
        },
        expansionItem3: {
          name: "-",
          value: "-",
        },
        expansionItem4: {
          name: "-",
          value: "-",
        },
        expansionItem5: {
          name: "-",
          value: "-",
        },
        expansionItem6: {
          name: "-",
          value: "-",
        },
      },
      formData: [],
    };
  },
  validations: {
    selected: {
      account: {
        value: { required },
      },
      expenseItem: {
        value: { required },
      },
      expense: {
        value: { required, mustBeGreaterThanZero },
      },
    },
  },
  computed: {
    fields() {
      return [
        {
          key: "selectRow",
          label: this.$t("SELECT"),
          class: "text-center normal-width",
        },
        {
          key: "account",
          label: this.$t("INPUTEXPENSE.account"),
          class: "text-center normal-width",
        },
        {
          key: "expenseItem",
          label: this.$t("INPUTEXPENSE.expenseItem"),
          class: "text-center normal-width",
        },
        {
          key: "expense",
          label: this.$t("INPUTEXPENSE.expense"),
          class: "text-center normal-width",
        },
        {
          key: "expansionItem1",
          label: this.$t("INPUTEXPENSE.expansionItem1"),
          class: "text-center normal-width",
        },
        {
          key: "expansionItem2",
          label: this.$t("INPUTEXPENSE.expansionItem2"),
          class: "text-center normal-width",
        },
        {
          key: "expansionItem3",
          label: this.$t("INPUTEXPENSE.expansionItem3"),
          class: "text-center normal-width",
        },
        {
          key: "expansionItem4",
          label: this.$t("INPUTEXPENSE.expansionItem4"),
          class: "text-center normal-width",
        },
        {
          key: "expansionItem5",
          label: this.$t("INPUTEXPENSE.expansionItem5"),
          class: "text-center normal-width",
        },
        {
          key: "expansionItem6",
          label: this.$t("INPUTEXPENSE.expansionItem6"),
          class: "text-center normal-width",
        },
      ];
    },
    ...mapGetters({
      selected_expense_item: "getSelectedExpenseItem",
      userDetails: "getUserDetails",
      client_details: "getClientData",
    }),
  },
  mounted() {
    this.getExpansionItemList();
  },
  created() {
    this.getTreeData();
  },
  methods: {
    async submit() {
      console.log("SUBMit function");
    },
    /**
     * @description function to fetch selected row items on check
     * @name toggleSelectAll
     */
    toggleSelectAll(item) {
      this.$refs.selectableTable.selectRow(item.index);
      item.item.selectRow = !item.item.selectRow;
      let selectedItem = [item.item];
      this.onRowSelected(selectedItem);
      this.refreshStore();
    },
    /**
     * @description function to get selected row items
     * @name onRowSelected
     */
    onRowSelected(items) {
      if (items.length > 0) {
        let data = items[0];
        let relatedFlag = [];
        this.selected = {
          account: {
            name: data.account.name,
            value: data.account.value,
            code: data.account.code,
          },
          expenseItem: {
            name: data.expenseItem.name,
            value: data.expenseItem.value,
            code: data.expenseItem.code,
          },
          expense: {
            name: data.expense.name,
            value: Number(
              data.expense.value.substring(0).replace(/[^0-9.-]+/g, "")
            ),
          },
          expansionItem1: {
            name: data.expansionItem1.name,
            value:
              data.expansionItem1.value == "-"
                ? ""
                : data.expansionItem1.kbn == "02"
                ? Number(
                    data.expansionItem1.value
                      .substring(0)
                      .replace(/[^0-9.-]+/g, "")
                  )
                : data.expansionItem1.value,
            id: data.expansionItem1.id,
            kbn: data.expansionItem1.kbn,
          },
          expansionItem2: {
            name: data.expansionItem2.name,
            value:
              data.expansionItem2.value == "-"
                ? ""
                : data.expansionItem2.kbn == "02"
                ? Number(
                    data.expansionItem2.value
                      .substring(0)
                      .replace(/[^0-9.-]+/g, "")
                  )
                : data.expansionItem2.value,
            id: data.expansionItem2.id,
            kbn: data.expansionItem2.kbn,
          },
          expansionItem3: {
            name: data.expansionItem3.name,
            value:
              data.expansionItem3.value == "-"
                ? ""
                : data.expansionItem3.kbn == "02"
                ? Number(
                    data.expansionItem3.value
                      .substring(0)
                      .replace(/[^0-9.-]+/g, "")
                  )
                : data.expansionItem3.value,
            id: data.expansionItem3.id,
            kbn: data.expansionItem3.kbn,
          },
          expansionItem4: {
            name: data.expansionItem4.name,
            value:
              data.expansionItem4.value == "-"
                ? ""
                : data.expansionItem4.kbn == "02"
                ? Number(
                    data.expansionItem4.value
                      .substring(0)
                      .replace(/[^0-9.-]+/g, "")
                  )
                : data.expansionItem4.value,
            id: data.expansionItem4.id,
            kbn: data.expansionItem4.kbn,
          },
          expansionItem5: {
            name: data.expansionItem5.name,
            value:
              data.expansionItem5.value == "-"
                ? ""
                : data.expansionItem5.kbn == "02"
                ? Number(
                    data.expansionItem5.value
                      .substring(0)
                      .replace(/[^0-9.-]+/g, "")
                  )
                : data.expansionItem5.value,
            id: data.expansionItem5.id,
            kbn: data.expansionItem5.kbn,
          },
          expansionItem6: {
            name: data.expansionItem6.name,
            value:
              data.expansionItem6.value == "-"
                ? ""
                : data.expansionItem6.kbn == "02"
                ? Number(
                    data.expansionItem6.value
                      .substring(0)
                      .replace(/[^0-9.-]+/g, "")
                  )
                : data.expansionItem6.value,
            id: data.expansionItem6.id,
            kbn: data.expansionItem6.kbn,
          },
        };
        for (let i in this.$t("ARRAYDATA.Array")) {
          let m = `expansionItem${parseInt(i) + 1}`;
          let keyName = this.selected[m];
          if (keyName.name != "-") {
            relatedFlag.push(true);
          } else {
            relatedFlag.push(false);
          }
        }
        if (relatedFlag.includes(true)) {
          this.relatedFlagInfo = true;
        } else {
          this.relatedFlagInfo = false;
        }
      } else {
        this.relatedFlagInfo = false;
        this.selected = this.formStyle;
      }
    },
    /**
     * @description function to Fetch previous year and month data of expenseitem info
     * @name onPreviousClick
     */
    onPreviousClick() {
      this.month = parseInt(this.month) - 1;
      if (this.yearStartMonth > parseInt(this.month)) {
        this.financialYear = parseInt(this.year) - 1;
        if (this.month <= 0) {
          this.year = parseInt(this.year) - 1;
          this.month = 12;
        } else if (this.month > 9) {
          this.month = this.month;
        } else {
          this.month = "0" + this.month;
        }
      } else {
        if (this.month <= 0) {
          this.year = parseInt(this.year) - 1;
          this.month = 12;
        } else if (this.month > 9) {
          this.month = this.month;
        } else {
          this.month = "0" + this.month;
        }
      }
      this.getExpansionItemList();
      this.refreshStore();
      let item = [];
      this.onRowSelected(item);
    },
    /**
     * @description function to Fetch next year and month data of expenseitem info
     * @name onNextClick
     */
    onNextClick() {
      this.month = parseInt(this.month) + 1;
      if (this.month > 12) {
        this.year = parseInt(this.year) + 1;
        this.month = "01";
      } else if (this.month > 9) {
        this.month = this.month;
      } else {
        this.month = "0" + this.month;
      }

      if (this.yearStartMonth > parseInt(this.month)) {
        this.financialYear = parseInt(this.year) - 1;
      } else {
        this.financialYear = parseInt(this.year);
      }
      this.getExpansionItemList();
      this.refreshStore();
      let item = [];
      this.onRowSelected(item);
    },
    /**
     * @description function to call journalStatementImport page
     * @name journalStatementimport
     */
    Journalstatementimport() {},
    /**
     * @description function to fetch the expenseItemName And accountName
     * @name getTreeData
     */
    async getTreeData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.READWITHJOINMEXPENSEITEM,
        {
          paginate: false,
          limit: 5000,
          fieldlist: [
            "m_accounts_name.accounts_name_cd",
            "m_accounts_name.accounts_name_nm",
            "m_expense_item.expense_item_cd",
            "m_expense_item.expense_item_nm",
          ],
          conditions: [
            {
              key: "m_accounts_name.use_flg",
              value: true,
            },
            {
              key: "m_expense_item.use_flg",
              value: true,
            },
          ],
          joins: [
            {
              table: "m_accounts_name",
              join_type: "inner",
              join_conditions: [
                {
                  join_using: "m_accounts_name.accounts_name_cd",
                  join_operator: "=",
                  join_with: "m_expense_item.accounts_name_cd",
                },
              ],
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data.data;
        this.allTreeData = responseData;
        let mapData = [];
        let groupedData = this._.groupBy(responseData, "accounts_name_nm");
        Object.keys(groupedData).forEach((key) => {
          let childrenArray = [];
          groupedData[key].forEach((item) => {
            let bindChildData = {
              text: item.expense_item_nm,
            };
            childrenArray.push(bindChildData);
          });
          let bindTreeData = {
            text: key,
            state: { expanded: true },
            children: childrenArray,
          };
          mapData.push(bindTreeData);
        });
        this.$store.dispatch("updateTree", mapData);
      }
      this.refreshStore();
    },
    /**
     * @description function to register  the data of expansion items and account details to the t_cost_info tabel
     * @name registerExpenseItem
     */
    async registerExpenseItem() {
      this.$v.selected.$touch();
      this.loading = true;
      if (!this.$v.selected.$invalid) {
        const { data } = await BasicInfoApis.postAPI(
          endPoint.MASTERREADDATATCOSTINFO,
          {
            fieldlist: ["*"],
            conditions: [
              {
                key: "client_no",
                value: this.client_details.client_no,
              },
              {
                key: "cost_monthly",
                value: this.year.toString() + this.month,
              },
              {
                key: "accounts_name_cd",
                value: this.selected.account.code,
              },
              {
                key: "expense_item_cd",
                value: this.selected.expenseItem.code,
              },
            ],
          }
        );
        this.loading = false;
        if (data.success) {
          let responseData = data.data;
          if (responseData.length > 0) {
            let updateData = {
              cost: parseInt(this.selected.expense.value),
              update_id: this.client_details.client_no,
              update_dt: getDateHourMinute(new Date()),
            };
            let updationAPI = {
              data: [updateData],
              conditions: [
                {
                  key: "cost_info_no",
                  value: responseData[0].cost_info_no,
                },
              ],
            };

            const { data } = await BasicInfoApis.postAPI(
              endPoint.MASTERUPDATETCOSTINFO,
              updationAPI
            );
            if (data.success) {
              let responseClientInfo = {
                cost_info_no: responseData[0].cost_info_no,
              };
              let updateExpansionItemArray = [];
              for (let i in this.$t("ARRAYDATA.Array")) {
                let m = `expansionItem${parseInt(i) + 1}`;
                let keyName = this.selected[m];
                if (keyName != undefined && keyName.name != "-") {
                  let updateExpansionData = {
                    cost_expansion_item_no: keyName.id,
                    expansion_item_value:
                      keyName.value == "-" ? "" : keyName.value.toString(),
                    update_id: this.client_details.client_no,
                    update_dt: getDateHourMinute(new Date()),
                  };
                  let updateExpansionItemAPI = {
                    data: [updateExpansionData],
                    conditions: [
                      {
                        key: "cost_info_no",
                        value: responseClientInfo.cost_info_no,
                      },
                      {
                        key: "cost_expansion_item_no",
                        value: keyName.id,
                      },
                    ],
                  };
                  updateExpansionItemArray.push(updateExpansionItemAPI);
                }
              }
              if (updateExpansionItemArray.length > 0) {
                const { data } = await BasicInfoApis.postAPI(
                  endPoint.EVALUATIONUPDATECOSTEXPANSIONINFO,
                  updateExpansionItemArray
                );
                if (data.success) {
                  this.$v.$reset();
                  this.selected = this.formStyle;
                  this.getExpansionItemList();
                } else {
                  console.log(this.$t("ERRORMESSAGE"));
                }
              } else {
                this.$v.$reset();
                this.selected = this.formStyle;
                this.getExpansionItemList();
              }
            }
          } else {
            let insertData = {
              client_no: this.client_details.client_no,
              cost_monthly: this.year.toString() + this.month,
              accounts_name_cd: this.selected.account.code,
              expense_item_cd: this.selected.expenseItem.code,
              cost: parseInt(this.selected.expense.value),
              judg_sts_flg: false,
              judg_dt: getDateHourMinute(new Date()),
              del_flg: false,
              create_id: this.client_details.client_no,
              create_dt: getDateHourMinute(new Date()),
              update_id: this.client_details.client_no,
              update_dt: getDateHourMinute(new Date()),
            };
            const {
              data,
            } = await BasicInfoApis.postAPI(endPoint.MASTERCREATETCOSTINFO, [
              insertData,
            ]);
            if (data.success) {
              let clientInfo = parseInt(data.data[0]);
              let responseClientInfo = { cost_info_no: clientInfo };
              let expansionItemArray = [];
              for (let i in this.$t("ARRAYDATA.Array")) {
                let m = `expansionItem${parseInt(i) + 1}`;
                let keyName = this.selected[m];
                if (keyName != undefined && keyName.name != "-") {
                  let createExpansionData = {
                    cost_info_no: responseClientInfo.cost_info_no,
                    cost_expansion_item_no: keyName.id,
                    expansion_item_nm: keyName.name,
                    expansion_item_format_kbn: keyName.kbn,
                    expansion_item_value: keyName.value,
                    del_flg: false,
                    create_id: this.client_details.client_no,
                    create_dt: getDateHourMinute(new Date()),
                    update_id: this.client_details.client_no,
                    update_dt: getDateHourMinute(new Date()),
                  };
                  expansionItemArray.push(createExpansionData);
                }
              }
              if (expansionItemArray.length > 0) {
                const { data } = await BasicInfoApis.postAPI(
                  endPoint.EVALUATIONCREATECOSTEXPANSIONINFO,
                  expansionItemArray
                );
                if (data.success) {
                  this.$v.$reset();
                  this.selected = this.formStyle;
                  this.refreshStore();
                  this.getExpansionItemList();
                } else {
                  this.$v.$reset();
                  this.selected = this.formStyle;
                  this.getExpansionItemList();
                }
              } else {
                this.$v.$reset();
                this.selected = this.formStyle;
                this.refreshStore();
                this.getExpansionItemList();
              }
            }
          }
        }
      }
      this.loading = false;
    },
    /**
     * @description function to update  the data of expansion items value to the t_cost_info tabel
     * @name updateExpenseItem
     */
    updateExpenseItem(item) {
      this.selectedExpenseItem = item;
      let treeItem = this._.find(this.allTreeData, { expense_item_nm: item });
      this.$store.dispatch("updateSelectedExpenseItem", {
        accounts_name_cd: treeItem.accounts_name_cd,
        expense_item_cd: treeItem.expense_item_cd,
        expense_item_nm: treeItem.expense_item_nm,
      });
      this.selectedTreeItem = [treeItem];
    },
     refreshStore(){
      this.$store.dispatch("updateSelectedExpenseItem", {
        accounts_name_cd: "",
        expense_item_cd: "",
        expense_item_nm: "",
      });
      this.selectedTreeItem = [];
    },
    /**
     * @description function to show the expenseItem pop-up model
     * @name addExpenseItem
     */
    addExpenseItem() {
      this.$refs["tree-modal"].show();
    },
    /**
     * @description function to clear the form details
     * @name clearExpenseForm
     */
    clearExpenseForm() {
      this.$v.$reset();
      //this.refreshStore();
      this.selected = {
        account: {
          name: this.selected.account.name
            ? this.selected.account.name
            : this.$t("INPUTEXPENSE.account"),
           value: this.selected.account.value
            ? this.selected.account.value
            : "",
          code: this.selected.account.code ? this.selected.account.code : "",
        },
        expenseItem: {
          name: this.selected.expenseItem.name
            ? this.selected.expenseItem.name
            : this.$t("INPUTEXPENSE.expenseItem"),
          value: this.selected.expenseItem.value
            ? this.selected.expenseItem.value
            : "",
          code: this.selected.expenseItem.code
            ? this.selected.expenseItem.code
            : "",
        },
        expense: {
          name: this.selected.expense.name
            ? this.selected.expense.name
            : this.$t("INPUTEXPENSE.expense"),
          value: "",
        },
        expansionItem1: {
          name:
            this.selected.expansionItem1 &&
            this.selected.expansionItem1.name != "-"
              ? this.selected.expansionItem1.name
              : "-",
          value:
            this.selected.expansionItem1 &&
            this.selected.expansionItem1.value != "-"
              ? ""
              : "-",
          id:
            this.selected.expansionItem1 &&
            this.selected.expansionItem1.id != "-"
              ? this.selected.expansionItem1.id
              : "-",
          kbn:
            this.selected.expansionItem1 && this.selected.expansionItem1.kbn
              ? this.selected.expansionItem1.kbn
              : "-",
        },
        expansionItem2: {
          name:
            this.selected.expansionItem2 &&
            this.selected.expansionItem2.name != "-"
              ? this.selected.expansionItem2.name
              : "-",
          value:
            this.selected.expansionItem2 &&
            this.selected.expansionItem2.value != "-"
              ? ""
              : "-",
          id:
            this.selected.expansionItem2 &&
            this.selected.expansionItem2.id != "-"
              ? this.selected.expansionItem2.id
              : "-",
          kbn:
            this.selected.expansionItem2 &&
            this.selected.expansionItem2.kbn != "-"
              ? this.selected.expansionItem2.kbn
              : "-",
        },
        expansionItem3: {
          name:
            this.selected.expansionItem3 &&
            this.selected.expansionItem3.name != "-"
              ? this.selected.expansionItem3.name
              : "-",
          value:
            this.selected.expansionItem3 &&
            this.selected.expansionItem3.value != "-"
              ? ""
              : "-",
          id:
            this.selected.expansionItem3 &&
            this.selected.expansionItem3.id != "-"
              ? this.selected.expansionItem3.id
              : "-",
          kbn:
            this.selected.expansionItem3 &&
            this.selected.expansionItem3.kbn != "-"
              ? this.selected.expansionItem3.kbn
              : "-",
        },
        expansionItem4: {
          name:
            this.selected.expansionItem4 &&
            this.selected.expansionItem4.name != "-"
              ? this.selected.expansionItem4.name
              : "-",
          value:
            this.selected.expansionItem4 &&
            this.selected.expansionItem4.value != "-"
              ? ""
              : "-",
          id:
            this.selected.expansionItem4 &&
            this.selected.expansionItem4.id != "-"
              ? this.selected.expansionItem4.id
              : "-",
          kbn:
            this.selected.expansionItem4 &&
            this.selected.expansionItem4.kbn != "-"
              ? this.selected.expansionItem4.kbn
              : "-",
        },
        expansionItem5: {
          name:
            this.selected.expansionItem5 &&
            this.selected.expansionItem5.name != "-"
              ? this.selected.expansionItem5.name
              : "-",
          value:
            this.selected.expansionItem5 &&
            this.selected.expansionItem5.value != "-"
              ? ""
              : "-",
          id:
            this.selected.expansionItem5 &&
            this.selected.expansionItem5.id != "-"
              ? this.selected.expansionItem5.id
              : "-",
          kbn:
            this.selected.expansionItem5 &&
            this.selected.expansionItem5.kbn != "-"
              ? this.selected.expansionItem5.kbn
              : "-",
        },
        expansionItem6: {
          name:
            this.selected.expansionItem6 &&
            this.selected.expansionItem6.name != "-"
              ? this.selected.expansionItem6.name
              : "-",
          value:
            this.selected.expansionItem6 &&
            this.selected.expansionItem6.value != "-"
              ? ""
              : "-",
          id:
            this.selected.expansionItem6 &&
            this.selected.expansionItem6.id != "-"
              ? this.selected.expansionItem6.id
              : "-",
          kbn:
            this.selected.expansionItem6 &&
            this.selected.expansionItem6.kbn != "-"
              ? this.selected.expansionItem6.kbn
              : "-",
        },
      };
      this.$refs.selectableTable.clearSelected();
    },
    /**
     * @description function to convert number to curency
     * @name filterByCurrency
     */
    filterByCurrency(value) {
      try {
        return parseInt(value)
          .toString()
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
      } catch {
        return 0;
      }
    },
    /**
     * @description function to fetch the details of expansion item list
     * @name getExpansionItemList
     */
    async getExpansionItemList() {
      this.isBusy = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.EVALUATIONCLIENTCOSTEXPANSIONITEMLIST,
        {
          client_no: this.client_details.client_no,
          year: this.year,
          month: this.month,
        }
      );
      if (data.success) {
        let responseData = data.data.clientCostExpansionInfoListObject;
        if (this.month == "" && this.year == "") {
          this.year = data.data.year;
          this.month = data.data.month;
          this.getInputAgencyFlag();
        }
        if (responseData.length > 0) {
          this.errorMessage = "";
          let rowData = [];
          let disabledItems = [];
          responseData.map((item) => {
            disabledItems.push(item.expense_item_nm);
            let expansionItem = [];
            // Adding expansion Item to the expenseItem Array
            for (let i in [0, 1, 2, 3, 4, 5, 6]) {
              let filterCondition =
                item.expansion_items[i] &&
                item.expansion_items[i].expansion_item_format_kbn != null
                  ? item.expansion_items[i].expansion_item_format_kbn
                  : "";
              expansionItem.push({
                name:
                  item.expansion_items[i] &&
                  item.expansion_items[i].expansion_item_nm != null
                    ? item.expansion_items[i].expansion_item_nm
                    : "-",
                value:
                  item.expansion_items[i] &&
                  item.expansion_items[i].expansion_item_value != null
                    ? filterCondition == "02"
                      ? this.filterByCurrency(
                          item.expansion_items[i].expansion_item_value
                        )
                      : item.expansion_items[i].expansion_item_value
                    : "-",
                id:
                  item.expansion_items[i] &&
                  item.expansion_items[i].cost_expansion_item_no != null
                    ? item.expansion_items[i].cost_expansion_item_no
                    : "-",
                kbn:
                  item.expansion_items[i] &&
                  item.expansion_items[i].expansion_item_format_kbn != null
                    ? item.expansion_items[i].expansion_item_format_kbn
                    : "-",
              });
            }
            let data = {
              selectRow: false,
              expansionItem1: expansionItem[0],
              expansionItem2: expansionItem[1],
              expansionItem3: expansionItem[2],
              expansionItem4: expansionItem[3],
              expansionItem5: expansionItem[4],
              expansionItem6: expansionItem[5],
              account: {
                name: this.$t("INPUTEXPENSE.account"),
                value: item.accounts_name_nm,
                code: item.accounts_name_cd,
              },
              expenseItem: {
                name: this.$t("INPUTEXPENSE.expenseItem"),
                value: item.expense_item_nm,
                code: item.expense_item_cd,
              },
              expense: {
                name: this.$t("INPUTEXPENSE.expense"),
                value: this.filterByCurrency(item.cost),
              },
            };
            rowData.push(data);
          });
          this.isBusy = false;
          this.disabledExpenseItems = disabledItems;
          this.inputExpenseItemTableData = rowData;
          if (this.selected.account.value == "") this.relatedFlagInfo = false;
        } else {
          this.isBusy = false;
          this.inputExpenseItemTableData = [];
          this.disabledExpenseItems = [];
          if (this.selected.account.value == "") this.relatedFlagInfo = false;
        }
      } else {
        this.errorMessage = this.$t("GENRALDATAERRORMESSAGE");
        this.checkFlag = true;
        this.isBusy = false;
        this.inputExpenseItemTableData = [];
        if (this.selected.account.value == "") {
          this.relatedFlagInfo = false;
        }
        this.disabledExpenseItems = [];
        this.year = new Date().getFullYear();
        let currentMonth = new Date().getMonth();
        if (currentMonth < 9) {
          this.month = "0" + (parseInt(currentMonth) + 1); //setting the monthFormat as (MM) so adding 0 to month upto 9
        } else {
          this.month = parseInt(currentMonth) + 1;
        }
        this.financialYear = this.year;
        this.getInputAgencyFlag();
      }
    },
    /**
     * @description function to fetch the year and month
     * @name getInputAgencyFlag
     */
    async getInputAgencyFlag() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMCLIENTOTHERINFO,
        {
          fieldlist: ["client_no", "input_agency_option", "year_start_month"],
          conditions: [
            {
              key: "client_no",
              value: this.client_details.client_no,
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        if (responseData.length > 0) {
          this.agencyFlag = responseData[0].input_agency_option;
          this.yearStartMonth = responseData[0].year_start_month ? responseData[0].year_start_month : 1;
          if (this.yearStartMonth > parseInt(this.month)) {
            this.financialYear = parseInt(this.year) - 1;
          } else {
            this.financialYear = parseInt(this.year);
          }
        }
      }
    },
    /**
     * @description function to fetch the ExpansionData
     * @name getExpansionItem
     */
    async getExpansionItem() {
      this.selectLoader = true;
      let selectedItem = this.selectedTreeItem[0];
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADMCOSTEXPANSIONITEM,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "accounts_name_cd",
              value: selectedItem.accounts_name_cd,
            },
            {
              key: "expense_item_cd",
              value: selectedItem.expense_item_cd,
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        let expansionItem = [];
        responseData.map((item) => {
          expansionItem.push({
            name: item ? item.expansion_item_nm : "-",
            value: "",
            id: item ? item.cost_expansion_item_no : "-",
            kbn: item ? item.expansion_item_format_kbn : "-",
          });
        });

        this.selected = {
          expansionItem1: expansionItem[0],
          expansionItem2: expansionItem[1],
          expansionItem3: expansionItem[2],
          expansionItem4: expansionItem[3],
          expansionItem5: expansionItem[4],
          expansionItem6: expansionItem[5],
          account: {
            name: this.$t("INPUTEXPENSE.account"),
            value: selectedItem.accounts_name_nm,
            code: selectedItem.accounts_name_cd,
          },
          expenseItem: {
            name: this.$t("INPUTEXPENSE.expenseItem"),
            value: selectedItem.expense_item_nm,
            code: selectedItem.expense_item_cd,
          },
          expense: {
            name: this.$t("INPUTEXPENSE.expense"),
            value: "",
          },
        };
        let relatedFlag = [];
        for (let i in this.$t("ARRAYDATA.Array")) {
          let m = `expansionItem${parseInt(i) + 1}`;
          let keyName = this.selected[m];
          if (keyName != undefined && keyName.value != "-") {
            relatedFlag.push(true);
          } else {
            relatedFlag.push(false);
          }
        }
        if (relatedFlag.includes(true)) {
          this.relatedFlagInfo = true;
        } else {
          this.relatedFlagInfo = false;
        }
        this.selectLoader = false;
        this.getExpansionItemList();
        this.selectedTreeItem = [];
        this.$refs["tree-modal"].hide();
      } else {
        this.selectLoader = false;
        this.$refs["tree-modal"].hide();
      }
    },
    rowClass() {
      return "t-bottom-border";
    },
  },
};
</script>
